import { useState, useEffect } from 'react';
import { utils, BigNumber } from 'ethers';

import { Box, Button } from '@material-ui/core';

import { useContractFunction, useEthers } from '@usedapp/core';

import { LOTO_HASH_LOTTERY_CONTRACT } from '../../config';

import { fMCurrency } from '../../utils/formatNumber';

const BuyTicketButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [usingDisabled, setUsingDisabled] = useState(true);

  const [buttonText, setButtonText] = useState(`Buy Ticket for  ${props.value.toString()} $JUICE`);

  useEffect(() => {
    if (props.account) {
      if (props.allowance && props.allowance.gte(utils.parseEther(props.value.toString()))) {
        setDisabled(false);
        setUsingDisabled(false);
      } else {
        setDisabled(true);
        setUsingDisabled(true);
      }
    } else {
      setUsingDisabled(true);
      setDisabled(true);
    }
  }, [props.value, props.account, props.allowance]);

  const { state, send } = useContractFunction(LOTO_HASH_LOTTERY_CONTRACT, 'placeBet', {
    transactionName: 'Buy Ticket'
  });

  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
      if (!usingDisabled) {
        setDisabled(false);
      }
      props.openFailedPopup('Failed buying a ticket');
      setButtonText(`Buy Ticket for  ${props.value.toString()} $JUICE`);
    }

    if (state.status === 'None') {
      if (!usingDisabled) {
        setUsingDisabled(false);
        setDisabled(false);
      }
      setButtonText(`Buy Ticket for  ${props.value.toString()} $JUICE`);
    }

    if (state.status === 'Success') {
      setDisabled(false);
      setButtonText(`Buy Ticket for  ${props.value.toString()} $JUICE`);
      props.openSuccessPopup('Successful buying a ticket');

      props.miningPopup(fMCurrency(utils.formatEther(props.miningRate.toString())));
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setUsingDisabled(false);
      setButtonText('Pending...');
    }
  }, [state]);

  const handleBuy = () => {
    if (props.account) {
      props.setOpen(false);
      send({ gasLimit: 5000000 });
    } else {
      props.setOpen(true);
    }
  };

  return (
    <Button
      size="large"
      variant="contained"
      color="carrot"
      className="hl-buy-btn"
      onClick={handleBuy}
      disabled={disabled}
    >
      {buttonText}
    </Button>
  );
};

export default BuyTicketButton;
