import { useEffect, useState } from 'react';
import { shortenAddress, shortenTransactionHash } from '@usedapp/core';

// ----------------------------------------------------------------------

// material
import {
  Table,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
  TableContainer,
  Tooltip,
  TablePagination,
  Link,
  Typography,
  Stack,
  Chip,
  Button
} from '@material-ui/core';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import ErrorIcon from '@material-ui/icons/Error';
import { useQuery } from '@apollo/client';
import { GET_ROUNDS_HASH_LOTTERY } from '../../queries';
import { hexToBytes, getSymbolPlaces } from '../../utils/encodeParams';
// components
import Scrollbar from '../Scrollbar';
import { fCRCurrency, fMCurrency } from '../../utils/formatNumber';

const ROUNDSCOLUMNS = [
  { id: 'block', label: 'Block', minWidth: 20 },
  { id: 'round', label: 'Round', minWidth: 10, align: 'center' },
  { id: 'drawnby', label: 'Drawn by', minWidth: 80 },
  { id: 'hash', label: 'Hash', minWidth: 150 },
  { id: 'rewards', label: 'Rewards', minWidth: 100 },
  { id: 'buyers', label: 'Buyers', minWidth: 40, align: 'center' },
  { id: 'winner', label: 'Winner', minWidth: 40, align: 'center' }
];

// ----------------------------------------------------------------------

function formatHashData(hash) {
  const bytes1 = hexToBytes(hash);
  const symbols1 = bytes1.map((row) => getSymbolPlaces(row));

  const hashData = [];

  for (let i = 0; i < 6; i += 1) {
    const symbol1 = {
      symbol: '',
      match: false
    };

    symbol1.symbol = symbols1[i];

    if (i >= 3) {
      symbol1.match = true;
    }

    hashData.push(symbol1);
  }

  return { hashData };
}

export default function RoundsTable(props) {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [total, setTotal] = useState(0);
  const [rounds, setRounds] = useState([]);

  const { data } = useQuery(GET_ROUNDS_HASH_LOTTERY, {
    variables: {
      perPage: rowsPerPage,
      skip: page * rowsPerPage
    },
    pollInterval: 3000
  });

  useEffect(() => {
    if (data) {
      const roundsData = data.rounds.map((row) => ({
        block: row.block != null ? row.block : '-',
        round: row.roundID,
        drawnby: row.drawnBy != null ? row.drawnBy : '-',
        hash: row.hash != null ? formatHashData(row.hash) : '-',
        rewards: row.rewards != null ? row.rewards : '0',
        buyers: row.totalBets != null ? row.totalBets : '-',
        winner: row.numberOfWinners != null ? row.numberOfWinners : '0',
        txid: row.tx
      }));

      setRounds(roundsData);

      if (data.summaries.length > 0) {
        setTotal(data.summaries[0].totalRounds);
      }
    }
  }, [data]);

  // const roundsData = (roundsBlock, roundsRound, roundsTicket, roundsRewards, rewardsTxId, roundsStarts, roundsEnds, roundsClaim) => {
  //   // GENERATE DATA HERE
  //   return { block: roundsBlock, round: roundsRound, ticket: roundsTicket, rewards: roundsRewards, txId: rewardsTxId, starts: roundsStarts, ends: roundsEnds, claim: roundsClaim };
  // }

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <>
      <Scrollbar>
        <TableContainer sx={{ minWidth: '65rem', maxHeight: '35rem', minHeight: '35rem' }}>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                {ROUNDSCOLUMNS.map((column) => (
                  <TableCell
                    key={column.id}
                    align={column.align}
                    style={{ top: 0, minWidth: column.minWidth, boxShadow: 'none' }}
                    sx={{ background: '#4c191e', fontSize: '1rem' }}
                  >
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>

            <TableBody style={{ top: 40 }}>
              {rounds.map((row) => (
                <TableRow hover role="checkbox" tabIndex={-1} key={row.round + row.block}>
                  {ROUNDSCOLUMNS.map((column) => {
                    const value = row[column.id];

                    // BLOCK
                    if (column.id === 'block') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            <Link
                              href={`https://bscscan.com/block/${value}`}
                              style={{ color: 'white' }}
                              target="_blank"
                            >
                              {value}
                            </Link>
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'round') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            {value}
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'drawnby') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            {value != '-' ? (
                              <Link
                                href={`https://bscscan.com/tx/${row.txid}`}
                                target="_blank"
                                style={{ color: 'white' }}
                              >
                                {shortenTransactionHash(value)}
                              </Link>
                            ) : (
                              value
                            )}
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'hash') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography
                            variant="subtitle1"
                            style={{
                              fontWeight: 400,
                              border: '2px solid orange',
                              padding: '0.4rem 0.8rem',
                              borderRadius: '8px',
                              width: 'fit-content'
                            }}
                          >
                            {row.hash != '-'
                              ? row.hash.hashData.map((hash, i) => (
                                  <Typography style={{ display: 'inline-block' }} color={hash.match ? 'primary' : ''}>
                                    {hash.symbol}
                                  </Typography>
                                ))
                              : row.hash}
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'rewards') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center">
                              <Link
                                href={`https://bscscan.com/tx/${row.txId}`}
                                target="_blank"
                                style={{ color: 'white' }}
                              >
                                {fMCurrency(value)} JUICE
                              </Link>
                              <img alt="" src="/static/icons/ic_juice.svg" width={20} height={20} />
                            </Stack>
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'buyers') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            {value}
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'winner') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            {value}
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'claim') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Button variant="contained" color="carrot" style={{ width: '100%' }}>
                            Claim
                          </Button>
                        </TableCell>
                      );
                    }

                    return '';
                  })}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Scrollbar>
      <TablePagination
        page={page}
        component="div"
        count={Number(total)}
        rowsPerPage={rowsPerPage}
        onPageChange={handleChangePage}
        rowsPerPageOptions={[10, 25, 50]}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </>
  );
}
