import { useEffect, useState } from 'react';
import { shortenAddress, shortenTransactionHash } from '@usedapp/core';

// ----------------------------------------------------------------------

// material
import {
  Table,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
  TableContainer,
  Tooltip,
  TablePagination,
  Link,
  Typography,
  Stack,
  Chip,
  Button
} from '@material-ui/core';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import ErrorIcon from '@material-ui/icons/Error';
import { useQuery } from '@apollo/client';
import { GET_USER_TICKETS_HASH_LOTTERY } from '../../queries';
import { hexToBytes, getSymbolPlaces } from '../../utils/encodeParams';
import { utils, BigNumber } from 'ethers';
import { useBlockNumber, useEthers, useContractFunction, useContractCalls } from '@usedapp/core';
import { LOTO_HASH_LOTTERY_ADDRESS, LOTO_HASH_LOTTERY_INTERFACE, LOTO_HASH_LOTTERY_CONTRACT } from 'src/config';

import { fCRCurrency, fMCurrency } from '../../utils/formatNumber';

import ClaimAllButton from './ClaimAllButton';

// components
import Scrollbar from '../Scrollbar';

const MYTICKETSCOLUMNS = [
  { id: 'block', label: 'Block', minWidth: 10 },
  { id: 'ticketID', label: 'Ticket ID', minWidth: 5 },
  { id: 'round', label: 'Round', minWidth: 5 },
  { id: 'ticket', label: 'Ticket', minWidth: 80 },
  { id: 'hash', label: 'Hash', minWidth: 10 },
  { id: 'rewards', label: 'Rewards', minWidth: 50 },
  { id: 'starts', label: 'Starts', minWidth: 20 },
  { id: 'ends', label: 'Ends', minWidth: 20 }
];

function formatHashData(hash) {
  const bytes1 = hexToBytes(hash);
  const symbols1 = bytes1.map((row) => getSymbolPlaces(row));

  const hashData = [];

  for (let i = 0; i < 6; i += 1) {
    const symbol1 = {
      symbol: '',
      match: false
    };

    symbol1.symbol = symbols1[i];

    if (i >= 3) {
      symbol1.match = true;
    }

    hashData.push(symbol1);
  }

  return { hashData };
}

function timeConverter(UNIX_timestamp) {
  var a = new Date(UNIX_timestamp * 1000);
  var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  var year = a.getFullYear();
  var month = months[a.getMonth()];
  var date = a.getDate();
  var hour = a.getHours();
  var min = a.getMinutes();
  var sec = a.getSeconds();

  if (min < 9) {
    min = '0' + min;
  }
  var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec;
  return time;
}

// ----------------------------------------------------------------------

export default function MyTickets(props) {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [total, setTotal] = useState(0);
  const [tickets, setTickets] = useState([]);

  const { account } = useEthers();
  const [user, setUser] = useState(undefined);

  useEffect(() => {
    if (account) {
      setUser(account.toLowerCase());
    } else {
      setUser(undefined);
    }
  }, [account]);

  const [claimable] = useContractCalls([
    {
      abi: LOTO_HASH_LOTTERY_INTERFACE,
      address: LOTO_HASH_LOTTERY_ADDRESS,
      method: 'getUserAvailableRewards',
      args: [account]
    }
  ]);

  const { data } = useQuery(GET_USER_TICKETS_HASH_LOTTERY, {
    variables: {
      perPage: rowsPerPage,
      skip: page * rowsPerPage,
      account: user
    },
    pollInterval: 3000
  });

  useEffect(() => {
    if (data) {
      const ticketsData = data.tickets.map((row) => ({
        block: row.block,
        ticketID: row.betID,
        round: row.round.id,
        ticket: formatHashData(row.hash),
        hash: row.round.hash != null ? formatHashData(row.round.hash) : '-',
        rewards:
          row.round.hash != null && row.round.hashMap === row.hashMap
            ? parseInt(row.round.rewards) / row.round.numberOfWinners
            : '0',
        txId: row.tx,
        starts:
          row.round.hash != null && row.round.hashMap === row.hashMap ? timeConverter(row.round.roundSettled) : '-',
        ends:
          row.round.hash != null && row.round.hashMap === row.hashMap
            ? timeConverter(parseInt(row.round.roundSettled) + 86400 * 90)
            : '-'
      }));

      setTickets(ticketsData);

      if (data.users.length > 0) {
        setTotal(data.users[0].totalTickets);
      }
    }
  }, [data]);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <>
      <Stack>
        {claimable && claimable[0].gt(utils.parseEther('0')) ? (
          <ClaimAllButton
            value={fMCurrency(utils.formatEther(claimable[0].toString()))}
            openSuccessPopup={props.openSuccessPopup}
            openFailedPopup={props.openFailedPopup}
          />
        ) : (
          ''
        )}

        <Scrollbar>
          <TableContainer sx={{ minWidth: '65rem', maxHeight: '35rem', minHeight: '35rem' }}>
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  {MYTICKETSCOLUMNS.map((column) => (
                    <TableCell
                      key={column.id}
                      align={column.align}
                      style={{ top: 0, minWidth: column.minWidth, boxShadow: 'none' }}
                      sx={{ background: '#4c191e', fontSize: '1rem' }}
                    >
                      {column.label}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>

              <TableBody style={{ top: 40 }}>
                {tickets.map((row) => (
                  <TableRow hover role="checkbox" tabIndex={-1} key={row.block + row.betID}>
                    {MYTICKETSCOLUMNS.map((column) => {
                      const value = row[column.id];

                      // BLOCK
                      if (column.id === 'block') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              <Link
                                href={`https://bscscan.com/block/${value}`}
                                style={{ color: 'white' }}
                                target="_blank"
                              >
                                {value}
                              </Link>
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (column.id === 'ticketID') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              <Link
                                href={`https://bscscan.com/address/${LOTO_HASH_LOTTERY_ADDRESS}#readContract`}
                                target="_blank"
                                style={{ color: 'white' }}
                              >
                                {value}
                              </Link>
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (column.id === 'round') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              {value}
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (column.id === 'ticket') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography
                              variant="subtitle1"
                              style={{
                                fontWeight: 400,
                                border: '2px solid orange',
                                padding: '0.4rem 0.8rem',
                                borderRadius: '8px',
                                width: 'fit-content'
                              }}
                            >
                              {row.ticket.hashData.map((hash, i) => (
                                <Typography style={{ display: 'inline-block' }} color={hash.match ? 'primary' : ''}>
                                  {hash.symbol}
                                </Typography>
                              ))}
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (column.id === 'hash') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography
                              variant="subtitle1"
                              style={{
                                fontWeight: 400,
                                border: '2px solid orange',
                                padding: '0.4rem 0.8rem',
                                borderRadius: '8px',
                                width: 'fit-content'
                              }}
                            >
                              {row.hash != '-'
                                ? row.hash.hashData.map((hash, i) => (
                                    <Typography style={{ display: 'inline-block' }} color={hash.match ? 'primary' : ''}>
                                      {hash.symbol}
                                    </Typography>
                                  ))
                                : row.hash}
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (column.id === 'rewards') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center">
                                <Link
                                  href={`https://bscscan.com/tx/${row.txId}`}
                                  target="_blank"
                                  style={{ color: 'white' }}
                                >
                                  {value} JUICE
                                </Link>
                                <img alt="" src="/static/icons/ic_juice.svg" width={20} height={20} />
                              </Stack>
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (column.id === 'starts') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              {value}
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (column.id === 'ends') {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              {value}
                            </Typography>
                          </TableCell>
                        );
                      }

                      return '';
                    })}
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Scrollbar>
      </Stack>

      <TablePagination
        page={page}
        component="div"
        count={Number(total)}
        rowsPerPage={rowsPerPage}
        onPageChange={handleChangePage}
        rowsPerPageOptions={[10, 25, 50]}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </>
  );
}
