import { useState, useEffect } from 'react';
import { utils, BigNumber } from 'ethers';

import { Box, Button } from '@material-ui/core';

import { useContractFunction, useEthers } from '@usedapp/core';

import { LOTO_HASH_LOTTERY_CONTRACT } from '../../config';

import { fMCurrency } from '../../utils/formatNumber';

const ClaimAllButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [usingDisabled, setUsingDisabled] = useState(false);

  const [buttonText, setButtonText] = useState(`Claim All (${props.value.toString()} $JUICE)`);

  const { state, send } = useContractFunction(LOTO_HASH_LOTTERY_CONTRACT, 'claimBets', {
    transactionName: 'Claim All'
  });

  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
      if (!usingDisabled) {
        setDisabled(false);
      }

      props.openFailedPopup('Failed to claim $JUICE');

      setButtonText(`Claim All (${props.value.toString()} $JUICE)`);
    }

    if (state.status === 'None') {
      if (!usingDisabled) {
        setUsingDisabled(false);
        setDisabled(false);
      }
      setButtonText(`Claim All (${props.value.toString()} $JUICE)`);
    }

    if (state.status === 'Success') {
      setDisabled(false);
      setButtonText(`Claim All (${props.value.toString()} $JUICE)`);

      props.openSuccessPopup('Successfully claimed $JUICE');
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setUsingDisabled(false);
      setButtonText('Pending...');
    }
  }, [state]);

  const handleClaim = () => {
    send();
  };

  return (
    <Button
      size="large"
      color="carrot"
      variant="contained"
      style={{ marginLeft: 'auto', marginBottom: '1rem', marginRight: '2rem' }}
      onClick={handleClaim}
      disabled={disabled}
    >
      {buttonText}
    </Button>
  );
};

export default ClaimAllButton;
