import { useState, useEffect } from 'react';
import { utils, BigNumber } from 'ethers';

import { Box, Button } from '@material-ui/core';

import { useContractFunction, useEthers } from '@usedapp/core';

import { LOTO_ERC20_CONTRACT, LOTO_HASH_LOTTERY_ADDRESS } from '../../config';

const ApproveButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [usingDisabled, setUsingDisabled] = useState(true);

  const [buttonText, setButtonText] = useState('Approve');

  const value = props.value !== '' ? utils.parseEther(props.value) : BigNumber.from('0');

  useEffect(() => {
    if (props.account) {
      if (value.gt(BigNumber.from('0'))) {
        setDisabled(false);
        setUsingDisabled(false);
      } else {
        setDisabled(true);
        setUsingDisabled(true);
      }
    } else {
      setUsingDisabled(true);
      setDisabled(true);
    }
  }, [props.value, props.account, props.balance]);

  const { state, send } = useContractFunction(LOTO_ERC20_CONTRACT, 'approve', { transactionName: 'Approve $JUICE' });

  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
      if (!usingDisabled) {
        setDisabled(false);
      }
      props.openFailedPopup('Failed approve $JUICE');
      setButtonText('Approve');
    }

    if (state.status === 'None') {
      if (!usingDisabled) {
        setDisabled(false);
      }
      setButtonText('Approve');
    }

    if (state.status === 'Success') {
      setButtonText('Approve');
      props.openSuccessPopup('Successful approve $JUICE');
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setUsingDisabled(false);
      setButtonText('Pending...');
    }
  }, [state]);

  const handleApprove = () => {
    if (props.account) {
      props.setOpen(false);
      send(LOTO_HASH_LOTTERY_ADDRESS, value);
    } else {
      props.setOpen(true);
    }
  };

  return (
    <Button size="large" variant="contained" color="carrot" disabled={disabled} onClick={handleApprove}>
      {buttonText}
    </Button>
  );
};

export default ApproveButton;
