import { useState } from 'react';
import Lottie from 'react-lottie';
import { motion } from 'framer-motion';

// material
import {
  Button,
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  Stack,
  Paper,
  styled,
  Typography,
  Link
} from '@material-ui/core';

import WinnerSpin from '../../assets/cryptodrop/lottie/winnerspin.json';

import RedConfetti from '../../assets/cryptodrop/lottie/redConfetti.json';
import Loading from '../../assets/cryptodrop/lottie/loadingVIP.json';

import LoseGame from '../../assets/cryptodrop/lottie/losegame.json';

import WinDance from '../../assets/hashlottery/win.json';
import TreasureLottie from '../../assets/envelopes/treasure.json';

import { varFadeIn, varFadeInUp, varWrapEnter, varFadeInRight, varZoomIn, varZoomOut } from '../animate';

// ----------------------------------------------------------------------

const AnimateContainer = styled(motion.div)(({ theme }) => ({}));

const winnerSpinOptions = {
  loop: true,
  autoplay: true,
  animationData: WinnerSpin,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const trophyOptions = {
  loop: true,
  autoplay: true,
  animationData: WinDance,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const treasureOptions = {
  loop: true,
  autoplay: true,
  animationData: TreasureLottie,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const redConfettiOptions = {
  loop: true,
  autoplay: true,
  animationData: RedConfetti,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const loseGameOptions = {
  loop: true,
  autoplay: true,
  animationData: LoseGame,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const loadingOptions = {
  loop: true,
  autoplay: true,
  animationData: Loading,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

export default function HashLotteryResult(props) {
  const handleClose = () => {
    props.setOpenResult(false);
  };

  const resultAnimation = props.resultLoad.animation;
  const loadingAnimation = props.loading.animation;
  const loadingDisplay = props.loading.display;
  const loadingKey = props.loading.key;

  return (
    <div style={{ width: '100%' }}>
      <Dialog open={props.openResult} style={{ marginInline: 'auto' }}>
        <AnimateContainer initial="initial" animate="animate" variants={varWrapEnter}>
          <DialogContent
            style={{
              padding: '0.2rem',
              overflow: 'hidden',
              background: 'linear-gradient(146deg, rgba(54,15,32,1) 0%, rgba(68,25,14,1) 100%)'
            }}
          >
            {/* RESULT LOADING */}
            <motion.div
              variants={loadingAnimation}
              style={{
                display: loadingDisplay,
                minHeight: '25rem',
                padding: '2rem',
                width: '90vw',
                maxWidth: '44rem',
                textAlign: 'center'
              }}
            >
              <Typography variant="h3" sx={{ mt: 2 }}>
                Processing
              </Typography>
              <Typography variant="subtitle1">Calculating the next hash...</Typography>
              <Lottie options={loadingOptions} height={100} width={100} isClickToPauseDisabled />
            </motion.div>

            {/* FINISHED RESULT */}
            <motion.div
              variants={resultAnimation}
              style={{ display: props.resultLoad.display }}
              key={`${props.resultLoad.key}1`}
            >
              <Stack
                direction="column"
                style={{ minHeight: '25rem', padding: '2rem', width: '90vw', maxWidth: '44rem' }}
              >
                <Stack direction="column" justifyContent="center" alignItems="center" marginBottom="0.5rem">
                  {props.totalPrize > 0 ? (
                    <Lottie
                      options={trophyOptions}
                      style={{ width: '90%', maxWidth: '30rem', marginBottom: '-2rem', zIndex: '2' }}
                      isClickToPauseDisabled
                    />
                  ) : (
                    <>
                      <Typography variant="h3" style={{ marginTop: '1rem' }}>
                        Try Again
                      </Typography>
                      <Lottie
                        options={loseGameOptions}
                        style={{ width: '10rem', height: '10rem' }}
                        isClickToPauseDisabled
                      />
                    </>
                  )}

                  {props.totalPrize > 0 ? (
                    <>
                      <Typography variant="h3" sx={{ mt: 5 }}>
                        You Win
                      </Typography>
                      <Typography variant="h3" className="pulse">
                        {props.totalPrize} BNB
                      </Typography>
                      <Typography variant="subtitle1" style={{ color: 'white', opacity: '0.4' }}>
                        You can claim your prize daily
                      </Typography>
                    </>
                  ) : (
                    <Typography variant="h5" color="#ffffff69" style={{ marginTop: '1rem' }}>
                      Better luck next time!
                    </Typography>
                  )}

                  {/* <Link
                    href={`https://bscscan.com/tx/${props.hash.completeLastHash}`}
                    target="_blank"
                    style={{ color: 'white', zIndex: '3' }}
                  >
                    {`TX: ${props.hash.startHash}...${props.hash.endHash}${props.hash.lastSixHash}`}
                  </Link> */}

                  {props.totalPrize > 0 && (
                    <motion.div
                      variants={varFadeIn}
                      style={{
                        width: '150%',
                        position: 'absolute',
                        top: '-2rem',
                        overflow: 'hidden',
                        maxWidth: '60rem',
                        zIndex: '1'
                      }}
                    >
                      <Lottie options={winnerSpinOptions} height="100%" width="100%" isClickToPauseDisabled />
                      {/* <Lottie options={treasureOptions} height="100%" width="100%" isClickToPauseDisabled /> */}
                    </motion.div>
                  )}

                  {/* {props.totalPrize > 0 && (
                    <Lottie
                      options={redConfettiOptions}
                      style={{ position: 'absolute' }}
                      height="100%"
                      width="100%"
                      isClickToPauseDisabled
                    />
                  )} */}
                </Stack>
              </Stack>
            </motion.div>
          </DialogContent>

          <DialogActions
            style={{
              minHeight: '5rem',
              background: 'linear-gradient(146deg, rgba(54,15,32,1) 0%, rgba(68,25,14,1) 100%)'
            }}
          >
            <Button onClick={handleClose} autoFocus style={{ zIndex: '3' }} color="carrot">
              Close
            </Button>
          </DialogActions>
        </AnimateContainer>
      </Dialog>
    </div>
  );
}
