export function createBetId() {
  return (
    '0x' +
    'xxxxxxxxxxyxxxxxxxxxxxxyxxxxxxxx'.replace(/[xy]/g, function (c) {
      let r = (Math.random() * 16) | 0,
        v = c == 'x' ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    })
  );
}

export function encodeCombination(combination) {
  return combination.map((value) => value.toString(16).padStart(2, '0')).join('');
}

