export function hexToBytes(hex) {
  for (var bytes = [], c = 2; c < hex.length; c += 2) bytes.push(parseInt(hex.substr(c, 2), 16));
  return bytes;
}

export function hexToLastBytes(hex) {
  for (var bytes = [], c = 10; c < hex.length; c += 2) bytes.push(parseInt(hex.substr(c, 2), 16));
  return bytes;
}
